'use client';
import Link from 'next/link';
import { Device, TelemetryPoint } from '../lib/types';
import { StatusBadge } from './StatusBadge';
import { Sparkline } from './Sparkline';

export function DeviceCard({ device, data }: { device: Device; data: TelemetryPoint[]; }) {
  return (
    <div className="card" style={{display:'grid', gridTemplateColumns:'1fr auto', gap:12}}>
      <div>
        <div style={{display:'flex', justifyContent:'space-between', alignItems:'center', marginBottom:8}}>
          <div style={{fontWeight:700}}>{device.name} <span style={{opacity:0.6, fontWeight:400}}>• {device.model}</span></div>
          <StatusBadge status={device.status} />
        </div>
        <div style={{opacity:0.7, fontSize:12, marginBottom:6}}>Device ID: {device.id}</div>
        <Sparkline data={data} width={280} height={70} />
      </div>
      <div style={{display:'grid', alignContent:'space-between'}}>
        <div>
          <div className="kpi">{(device.lastValue ?? 0).toFixed(2)}</div>
          <div style={{opacity:0.6, fontSize:12}}>last reading</div>
        </div>
        <Link className="btn" href={`/equipment/${encodeURIComponent(device.id)}`}>Open</Link>
      </div>
    </div>
  );
}
