'use client';
import { useEffect, useMemo, useState } from 'react';
import { Device, EquipmentStatus, TelemetryPoint } from '@/lib/types';
import { subscribeSSE } from '@/lib/client-sse';
import { DeviceCard } from '@/components/DeviceCard';

type DeviceWithData = { device: Device; telemetry: TelemetryPoint[]; };

async function fetchDevices(statusFilter: EquipmentStatus | ''): Promise<DeviceWithData[]> {
  const qs = statusFilter ? `?status=${encodeURIComponent(statusFilter)}` : '';
  const res = await fetch(`/api/devices${qs}`, { cache: 'no-store' });
  const json = await res.json();
  return json.devices as DeviceWithData[];
}

export default function DashboardPage() {
  const [statusFilter, setStatusFilter] = useState<EquipmentStatus|''>('');
  const [items, setItems] = useState<DeviceWithData[]>([]);
  const [loading, setLoading] = useState(true);

  const load = async () => {
    setLoading(true);
    try { setItems(await fetchDevices(statusFilter)); }
    finally { setLoading(false); }
  };

  useEffect(() => { load(); }, [statusFilter]);

  useEffect(()=>{
    const unsub = subscribeSSE('/api/realtime', (msg)=>{
      if (msg.type === 'device_update') {
        setItems(prev => prev.map(x => x.device.id === msg.deviceId ? { ...x, device: msg.device } : x));
      } else if (msg.type === 'telemetry') {
        setItems(prev => prev.map(x => {
          if (x.device.id !== msg.deviceId) return x;
          const nextData = [...x.telemetry, msg.point];
          if (nextData.length>300) nextData.shift();
          return { ...x, telemetry: nextData, device: { ...x.device, lastValue: msg.point.v, lastTs: new Date(msg.point.ts).toISOString() } };
        }));
      }
    });
    return () => unsub();
  }, []);

  const stats = useMemo(()=>{
    const total = items.length;
    const counts = { OK:0, MAINTENANCE:0, FAULT:0, EMERGENCY:0, OFFLINE:0 } as Record<EquipmentStatus,number>;
    for (const it of items) counts[it.device.status]++;
    return { total, counts };
  }, [items]);

  return (
    <div className="grid" style={{gridTemplateColumns:'repeat(12, 1fr)'}}>
      <section className="card" style={{gridColumn:'span 12'}}>
        <div className="row" style={{justifyContent:'space-between'}}>
          <h2 style={{margin:0}}>Live Equipment Dashboard</h2>
          <div className="row">
            <select value={statusFilter} onChange={e => setStatusFilter(e.target.value as any)}>
              <option value="">All statuses</option>
              <option value="OK">OK</option>
              <option value="MAINTENANCE">MAINTENANCE</option>
              <option value="FAULT">FAULT</option>
              <option value="EMERGENCY">EMERGENCY</option>
              <option value="OFFLINE">OFFLINE</option>
            </select>
            <button className="btn" onClick={load} disabled={loading}>{loading?'Loading…':'Refresh'}</button>
          </div>
        </div>
        <div className="row" style={{marginTop:12}}>
          <div className="card" style={{flex:'1 1 0'}}>
            <div>OK</div>
            <div className="kpi" style={{color:'#22c55e'}}>{stats.counts.OK}</div>
          </div>
          <div className="card" style={{flex:'1 1 0'}}>
            <div>MAINTENANCE</div>
            <div className="kpi" style={{color:'#f59e0b'}}>{stats.counts.MAINTENANCE}</div>
          </div>
          <div className="card" style={{flex:'1 1 0'}}>
            <div>FAULT</div>
            <div className="kpi" style={{color:'#ef4444'}}>{stats.counts.FAULT}</div>
          </div>
          <div className="card" style={{flex:'1 1 0'}}>
            <div>EMERGENCY</div>
            <div className="kpi" style={{color:'#be123c'}}>{stats.counts.EMERGENCY}</div>
          </div>
          <div className="card" style={{flex:'1 1 0'}}>
            <div>OFFLINE</div>
            <div className="kpi" style={{color:'#94a3b8'}}>{stats.counts.OFFLINE}</div>
          </div>
          <div className="card" style={{flex:'1 1 0'}}>
            <div>TOTAL</div>
            <div className="kpi">{stats.total}</div>
          </div>
        </div>
      </section>

      <section className="card" style={{gridColumn:'span 12'}}>
        <h3>Devices</h3>
        <div className="grid" style={{gridTemplateColumns:'repeat(2, 1fr)'}}>
          {items.map(({device, telemetry}) => (
            <DeviceCard key={device.id} device={device} data={telemetry} />
          ))}
        </div>
      </section>
    </div>
  );
}
