'use client';
import React from 'react';
import { TelemetryPoint } from '../lib/types';

export function Sparkline({ data, width=220, height=60 }: { data: TelemetryPoint[]; width?: number; height?: number; }) {
  if (!data || data.length < 2) return <svg width={width} height={height}></svg>;
  const xs = data.map(p=>p.ts), ys = data.map(p=>p.v);
  const minX = Math.min(...xs), maxX = Math.max(...xs);
  const minY = Math.min(...ys), maxY = Math.max(...ys);
  const pad = 4;
  const scaleX = (x:number) => pad + (x - minX) / (maxX - minX || 1) * (width - pad*2);
  const scaleY = (y:number) => height - pad - (y - minY) / (maxY - minY || 1) * (height - pad*2);
  const d = data.map((p,i)=>`${i?'L':'M'}${scaleX(p.ts).toFixed(1)},${scaleY(p.v).toFixed(1)}`).join(' ');
  const last = data[data.length-1];
  return (
    <svg width={width} height={height}>
      <path d={d} fill="none" stroke="currentColor" strokeWidth="2" opacity="0.9"/>
      <circle cx={scaleX(last.ts)} cy={scaleY(last.v)} r="2.5" />
    </svg>
  );
}
